::-webkit-scrollbar {
    width:4px;
    height:4px;
}
::-webkit-scrollbar-track {
    background:rgba(29, 29, 29, 0.8);
}
::-webkit-scrollbar-thumb {
    background:rgb(0, 0, 0, 0.8);
}

main{
    display:flex;
}

.projectsContainer{
    text-align: center;
    font-size: 3rem;
    font-weight: bold;
}

.repositoriesContainer{
    display:flex;
    flex-wrap:wrap;
    justify-content: space-evenly;
    text-align: left;
}

.repoContainer{
    text-decoration: none;
    width: 25vw;
    border-radius: 5px;
    border: 1px solid rgba(255, 255, 255, 0.08);
    padding: 2.5vh 2%;
    margin: 2vh 0px;
    color: white;
    font-size: 20px;
    transition: all .2s ease-in-out;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
}

.repoContainer:hover{
    transform: scale(1.1);
}

.repoTitle{
    font-weight: bold;
    padding-bottom: 2px;
    font-size: 1rem;
}

.repoDescription{
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: normal;
}

.repoDetails {
    display: flex;
    justify-content: space-between;
    font-size: 17px;
}

.repoTitleContainer, .repoLangContainer, .repoStarContainer, .repoForkContainer{
    display:flex;
}
