.aboutDiv{
    margin-top: -50px;
    z-index: 18;
    position: relative;
    margin-bottom: 10vh;
    display:flex;
    justify-content: center;
    flex-direction: row;
    background-color: white;
    color:black;
    margin-left:20vw;
    margin-right:20vw;
    padding-bottom: 5vh;
    padding-top: 5vh;
    padding-right: 3vw;
    padding-left: 3vw;
    border-radius: 4px;
    margin-bottom: 5vh;
    box-shadow: 0 0 6px 1px black;
}

.aboutTitle, .skillsTitle{
    display:flex;
    justify-content: center;
    text-align: left;
    font-size: 2rem;
}

.aboutContainer{
    display:flex;
    justify-content: center;
    flex-direction: column;
    width:50%;
    text-align: center;
}

.aboutTextContainer{
    display:flex;
}

.skillsContainer{
    display:flex;
    flex-direction: column;
    justify-content: center;
    width:50%;
    text-align: center;
}

.skillsText, .aboutText{
    width: 100%;
    font-size: 30px;
    text-align: left;
    font-size: 1.25rem;
    font-weight: 300;
}

.aboutText{
    padding-top: 5vh;
}

.skillsText{
    display:flex;
    justify-content: center;
}

li{
    list-style-type: disclosure-closed;
}