html {
  scroll-behavior: smooth;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.25rem;
  font-weight: 300;
}

body{
  margin: 0;
  color: white;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width:5px;
  height:4px;
}
::-webkit-scrollbar-track {
  background-image: url(../images/sky.png)
}

::-webkit-scrollbar-thumb {
  background:rgb(0, 0, 0, 0.8);
}

.top-container{
    margin-top: 35vh;
    z-index: 10;
}

.layer {
  background-position: bottom center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  min-height: 1402px;
  height: auto;
  position: fixed;
  z-index: -1;
}

.layer1 {
  background-image: url(../images/sky.png);
}

.layer2 {
  background-image: url(../images/clouds_1.png);
}

.layer3 {
  background-image: url(../images/rocks.png);
}

.layer4 {
  background-image: url(../images/clouds_2.png);
}

.layer5 {
  background-image: url(../images/ground_1.png);
}

.layer6 {
  background-image: url(../images/ground_2.png);
}

.layer7 {
  background-image: url(../images/ground_3.png);
}

.layer8 {
  background-image: url(../images/plant.png);
}

.headerTransition {
  position: absolute;
  top: 0;
  transform: translateY(1px);
  width: 100%;
  min-height: 1402px;
  height: auto;
  background-image: url(../images/ground_3.png);
  background-position: bottom center;
  background-size: cover;
  background-repeat: no-repeat;
}

.header {
  min-height: 1402px;
  height: auto;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
}

.navbar-custom {
  text-align: center;
  margin-bottom: 50px;
  display: flex;
  justify-content: center;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

h1{
  text-align: center;
  font-size: 4rem;
}

.description {
  width:100%;
  background: linear-gradient(
    to bottom,
    rgb(15,18,37) 0%,
    rgb(31, 37, 71) 45%,
    rgb(43, 51, 100) 74%,
    rgb(45, 52, 102) 100%
  );
  min-height: 700px;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.message {
  align-self: center;
}

.image{
  height: auto;
  width: auto;
  max-width: 1em;
  max-height: 1em;
  border-radius: 50%;
  opacity: 0.8;
  margin-bottom: -5px;
}