.navbar{
    position:fixed;
    top: 0;
    width: 100%;
    z-index: 50;
    background-color: transparent;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    text-align: center;
    color: white;
    padding-top: 2.5vh;
    padding-bottom: 2.5vh;
    transition: .5s ease-in-out;
    font-size: 2rem;
}

.navbarContainer{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    text-align: center;
}

a{
    color:white;
    text-decoration: none;
    transition: .5s;
    border-bottom: 1px solid transparent;
}

a:hover{
    border-color: white;
}
a:active{
    border-color: white;
}