.footer {
    background-color: rgb(45, 52, 102);
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    color: white;
    bottom: 0;
    width: 100%;
    font-size: 1rem;
}

.footerColumn, .footerRow{
    width: 100%;
    height:100%;
}

.footerRow{
    display:flex;
    justify-content: space-evenly;
    flex-direction: column;
    flex-wrap: wrap;
}

.footerColumn{
    display:flex;
    justify-content: center;
}

.footerColumn > * {
    margin-right: 1vw;
    margin-left: 1vw;
}