.formContainer{
    display:flex;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    width:50%;
}

.contactContainer{
    display:flex;
    text-align: center;
    flex-direction: column;
    width:50%;
}

.formDiv{
    display:flex;
    justify-content: center;
    flex-direction: row;
    background-color: white;
    color:black;
    margin-left:20vw;
    margin-right:20vw;
    padding-bottom: 5vh;
    padding-top: 5vh;
    padding-right: 3vw;
    padding-left: 3vw;
    border-radius: 4px;
    margin-bottom: 5vh;
    margin-top: 5vh;
    box-shadow: 0 0 6px 1px black;
}

.form{
    width:90%;
    text-align: center;
    font-size: 1.25rem;
}

input[type=text], input[type=submit], input[type=email], textarea{
    width: 100%;
    padding: 12px 20px;
    margin-top: 3vh;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

button[type=submit] {
    width: 30%;
    padding: 12px 20px;
    margin-top: 3vh;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 50px;
    box-sizing: border-box;
    cursor: pointer;
    background-color: rgb(45, 52, 102);
    color: white;
    transition: .5s;
}

button[type=submit]:hover{
    filter:brightness(85%);
}

.formTitle, .contactTitle{
    display:flex;
    justify-content: left;
    text-align: left;
    font-size: 1.25rem;
    font-weight: bold;
}

.contactText{
    text-align: left;
    margin-top: 3vh;
    font-size: 1.25rem;
}

.contactImages{
    display:flex;

}
.contactImages > a{
    padding-left: 2vw;
}
.contactImages > a:first-child{
    padding-left: 0vw;
}

.contactImages > a > .imageContainer{
    background-color: white;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    border: 2px solid rgb(45, 52, 102);
    transition: .5s;
    margin-top: 5vh;
}

.formButton{
    text-align: left;
}

.contactImages > a > .imageContainer:hover{
    background-color: rgb(45, 52, 102);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    border: 2px solid white;
}

.linkedInImage{
    scale: 0.7;
}